:root {
  --white: 255, 255, 255;
  --purple: 117, 19, 93;
  --color-white: rgb(var(--white));
  --color-white-opaque: rgba(var(--white), 7);
  --color-purple: rgb(var(--purple));
  --color-purple-opaque: rgba(var(--purple), .7);
  --color-white-smoke: #f5f5f5;
  --color-grey: #a2a2a2;
  --linear-gradient: linear-gradient(135deg, var(--color-purple-opaque) 31vw, transparent 31vw, transparent);
}

html {
  font-size: 62.5%;
}

@media only screen and (width <= 1200px) {
  html {
    font-size: 50%;
  }
}

body {
  box-sizing: border-box;
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: Inria Serif, serif;
}

h2 {
  color: var(--color-white-opaque);
  font-size: 6.5rem;
}

@media only screen and (width <= 700px) {
  h2 {
    font-size: 4.4rem;
  }
}

header {
  clip-path: polygon(0 0, 30vw 0, 0 30vw);
  background-color: var(--color-purple);
  color: var(--color-white);
  mix-blend-mode: multiply;
  width: 100vw;
  height: 100vw;
  font-size: 5vw;
  position: fixed;
}

header > span {
  border: 2px solid var(--color-white);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 10vw;
  height: 10vw;
  margin: 3vw;
  display: flex;
}

section:nth-child(odd) {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

section:nth-child(2n) {
  background-color: var(--color-white-smoke);
  justify-content: space-between;
  align-items: center;
  height: 25vh;
  padding: 2rem 3rem;
  display: flex;
}

@media only screen and (width <= 1000px) {
  section:nth-child(2n) {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 40vh;
    display: flex;
  }
}

section p {
  color: var(--color-grey);
  text-align: left;
  width: 16%;
  padding-right: 10rem;
  font-size: 2.4rem;
}

@media only screen and (width <= 1000px) {
  section p {
    text-align: center;
    width: 90%;
    max-width: 500px;
    padding-right: 0;
  }
}

section aside {
  color: var(--color-purple-opaque);
  transform-origin: 0 0;
  align-self: flex-end;
  font-family: Muli, sans-serif;
  font-size: 1.7rem;
  transform: rotate(-90deg);
}

@media only screen and (width <= 1000px) {
  section aside {
    transform-origin: 0 0;
    align-self: center;
    font-size: 1.7rem;
    transform: rotate(0);
  }
}

#parallax-1 {
  background-image: var(--linear-gradient), url("El_Condoriri_trek.21908014.jpeg");
}

#parallax-2 {
  background-image: var(--linear-gradient), url("Illiniza_Norte.c9946892.jpeg");
}

#parallax-3 {
  background-image: var(--linear-gradient), url("El_Chachani_summit.112ae70b.jpeg");
}

